<template>
	<div class="detailshow" :style="{height: ctHeight}">
		<div class="menus">
			<div class="menus-title">导航</div>
			<div v-for="(item, index) in menus" :key="index" class="menus-item" :class="['', { 'active': index === activeItem }]" @click="switchItem(index)">
				<span class="title">{{item.name}}</span>
			</div>
		</div>
		<div id="content" class="detailshow-content">
			<img id="img1" :src="require('@/assets/anli/bg1.jpg')" alt="">
			<img id="img2" :src="require('@/assets/anli/bg2.jpg')" alt="">
			<img id="img3" :src="require('@/assets/anli/bg3.jpg')" alt="">
			<img id="img4" :src="require('@/assets/anli/bg1.jpg')" alt="">
			<img id="img5" :src="require('@/assets/anli/bg2.jpg')" alt="">
			<img id="img6" :src="require('@/assets/anli/bg3.jpg')" alt="">
			<img id="img7" :src="require('@/assets/anli/bg1.jpg')" alt="">
			<img id="img8" :src="require('@/assets/anli/bg2.jpg')" alt="">
			<img id="img9" :src="require('@/assets/anli/bg3.jpg')" alt="">
			<img id="img10" :src="require('@/assets/anli/bg1.jpg')" alt="">
			<img id="img11" :src="require('@/assets/anli/bg2.jpg')" alt="">
			<img id="img12" :src="require('@/assets/anli/bg3.jpg')" alt="">
		</div>
	</div>
</template>

<script>
import axios from 'axios';
export default {
	name: 'Index',
	data () {
		return {
			ctHeight: (document.documentElement.clientHeight || document.body.clientHeight) + 'px',
			activeItem: 0,
			menus: [{
				id: 0,
				name: '1、项目介绍'
			},{
				id: 1,
				name: '2、项目介绍'
			},{
				id: 2,
				name: '3、项目介绍'
			},{
				id: 3,
				name: '4、项目介绍'
			},{
				id: 4,
				name: '5、项目介绍'
			},{
				id: 5,
				name: '6、项目介绍'
			},{
				id: 6,
				name: '7、项目介绍'
			},{
				id: 7,
				name: '8、项目介绍'
			},{
				id: 8,
				name: '9、项目介绍'
			},{
				id: 9,
				name: '10、项目介绍'
			},{
				id: 10,
				name: '11、项目介绍'
			},{
				id: 11,
				name: '12、项目介绍'
			}]
		}
	},
	methods: {
		switchItem(index) {
			this.activeItem = index;
			let scrollHeight = document.getElementById(`img${index + 1}`).offsetTop;
			document.getElementById('content').scrollTo(0, scrollHeight);
		}
	},
	mounted () {
		window.onLoad  = () => {
			
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	.detailshow {
		display: flex;
		overflow: hidden;
	}
	.menus {
		width: 16%;
		padding-left: 3%;
		background-color: #2C2C2C;
		&-title {
			display: flex;
			align-items: center;
			height: 80px;
			font-size: 29px;
			color: #ffffff;
		}
		&-item {
			display: flex;
			flex-direction: row;
			align-items: center;
			height: 55px;
			font-size: 17px;
			cursor: pointer;
			color: #FFFFFF;
			&.active {
				font-size: 20px;
			}
			&:hover {
				font-size: 20px;
			}
		}
	}
	.detailshow-content {
		width: 100%;
		overflow-y: scroll;
		img {
			width: 100%;
		}
	}
</style>



